.portrait {
  .portrait-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 100%;
  }

  .portrait-name {
    white-space: nowrap;
    margin-bottom: 5px;
  }

  .portrait-period {
    display: block;
    font-size: 0.9em;
    color: var(--text);
    opacity: 0.7;
  }
}